@charset "UTF-8";
/* ==================================================
 * 			 		 全局Sass变量
 * 	这里简单介绍下CSS的布局，这里采用主流的Flexbox,
 *  在高度上采取固定Px，宽度自适应, 在布局上有参考下
 *  Framework7 的布局,在一些宽度上采用rem来优化多终端的适应
 * ================================================= */
@import url(./normalize.css);
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none; }

body {
  font-family: "Microsoft YaHei UI", "Lucida Sans Typewriter";
  font-size: 12px;
  margin-left: 0;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  background-color: #F4F4F4;
  -webkit-text-size-adjust: 100%; }

ol,
ul {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  margin-bottom: 0; }

img {
  vertical-align: middle; }

p {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  margin-bottom: 0; }

.p {
  margin-bottom: 0.25rem; }

input, textarea {
  background: none;
  border: none;
  resize: none;
  outline: none; }

[role="button"] {
  cursor: pointer; }

.page {
  background: #ffffff; }

.pa {
  position: absolute; }

.pr {
  position: relative; }

.full {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden; }

.price {
  color: #ff9900; }

.flex {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important; }

.flex-col {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }

.flex-row {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row; }

.center-block {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.align-center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.mg-l-15 {
  margin-left: 0.375rem; }

.mg-r-15 {
  margin-right: 0.375rem; }

.mg-15 {
  margin-left: 0.375rem;
  margin-top: 0.375rem;
  margin-right: 0.375rem;
  margin-bottom: 0.375rem; }

.pd-l-15 {
  padding-left: 0.375rem; }

.pd-r-15 {
  padding-left: 0.375rem; }

.pd-15 {
  padding-left: 0.375rem;
  padding-top: 0.375rem;
  padding-right: 0.375rem;
  padding-bottom: 0.375rem; }

.pd-l-30 {
  padding-left: 0.75rem; }

.pd-r-30 {
  padding-right: 0.75rem; }

.mg-l-30 {
  margin-left: 0.75rem; }

.mg-r-30 {
  margin-right: 0.75rem; }

.mg-t-20 {
  margin-top: 0.5rem; }

.mg-t-10 {
  margin-top: 0.25rem; }

.mg-r-100 {
  margin-right: 2.5rem; }

.mg-r-60 {
  margin-right: 1.5rem; }

.show {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important; }

.hide {
  display: none !important; }

.clearfix:before, .clearfix:after {
  content: " ";
  display: table; }

.clearfix:after {
  clear: both; }

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto; }

.font-15 {
  font-size: 15px; }

.font-12 {
  font-size: 12px; }

.font-18 {
  font-size: 18px; }

.font-16 {
  font-size: 16px; }

.font-14 {
  font-size: 14px; }

.font-20 {
  font-size: 20px; }

.font-24 {
  font-size: 24px; }

.margin-t-10 {
  margin-top: 0.25rem; }



